import { Tabs, Tab } from 'react-bootstrap'
import dBank from '../abis/dBank.json'
import React, { Component } from 'react';
import Token from '../abis/Token.json'
import dbank from '../dbank.png';
import Web3 from 'web3';
import './App.css';

//h0m3w0rk - add new tab to check accrued interest

class App extends Component {

  async componentWillMount() {
    await this.loadBlockchainData(this.props.dispatch)
  }

  async loadBlockchainData(dispatch) {
    if (typeof window.ethereum!=='undefined'){

    const web3 = new Web3(window.ethereum)
    const netId = await web3.eth.net.getId()
    const accounts = await web3.eth.getAccounts()

    //load balance
    if(typeof accounts[0] !=='undefined'){
    
    const balance = await web3.eth.getBalance(accounts[0])

    this.setState({ account: accounts[0], balance: balance, web3 : web3})
  } else {
    window.alert('Please login with Metamask')    
  }
    //Token
    //Bank
    //load contracts
    try{
    const token = new web3.eth.Contract(Token.abi, Token.networks[netId].address)
    const dbank = new web3.eth.Contract(dBank.abi, dBank.networks[netId].address)
    const dBankAddress = dBank.networks[netId].address
    const tokenBalance = await token.methods.balanceOf(this.state.account).call()
    const appTokenBalance = await web3.utils.fromWei(tokenBalance)
    const balance = await web3.eth.getBalance(accounts[0])
    const appEtherBalance = await web3.utils.fromWei(balance)
    this.setState({token: token, dbank: dbank, dBankAddress: dBankAddress, 
      tokenBalance: tokenBalance, appTokenBalance: appTokenBalance, appEtherBalance: appEtherBalance})
  } catch (e) {
console.log('Error', e)
window.alert('Contracts not deployed to the current network')

  }


   
  } else {
    window.alert('Please install a Wallet Like MetaMask to use this application');
  }

      //assign to values to variables: web3, netId, accounts

      //check if account is detected, then load balance&setStates, elsepush alert

      //in try block load contracts

    //if MetaMask not exists push alert
  }

  async deposit(amount) {
   if(this.state.dbank!=='undefined'){
      try{
        await this.state.dbank.methods.deposit().send({value: amount.toString(), from: this.state.account})
      } catch (e) {
        console.log('Error, deposit: ', e)
      }
    }
  }

  async withdraw(e) {
    e.preventDefault()
    if(this.state.dbank!=='undefined'){
      try{
        await this.state.dbank.methods.withdraw().send({from: this.state.account})
      } catch(e) {
        console.log('Error, withdraw: ', e)
      }
    }
  }

  constructor(props) {
    super(props)
    this.state = {
      web3: 'undefined',
      account: '',
      token: null,
      dbank: null,
      balance: 0,
      dBankAddress: null,
      tokenBalance:0,
      appTokenBalance:0,
      appEtherBalance:0

    }
  }

  render() {
    return (
      <div className=''>
        <nav className="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow">
          <a
            className="navbar-brand col-sm-3 col-md-2 mr-0"
            href="http://www.dappuniversity.com/bootcamp"
            target="_blank"
            rel="noopener noreferrer"
          >
        
          <b>Crypto Banking</b>
        </a>
        </nav>
        <div className="container-fluid mt-5 text-center">
        <br></br>
          <h1>Welcome to Crypto Bank</h1>
          <span>Your Wallet Address</span>
          <h4>{this.state.account}</h4>
          <br></br>
          <div className="row">
            <main role="main" className="col-lg-12 d-flex text-center">
              <div className="content mr-auto ml-auto">
               <Tabs defaultActiveKey="deposit" id="uncontrolled-tab-example">
                <Tab eventKey="deposit" title="Deposit">
                  <div>
                  <br></br>
                    How much do you want to deposit?
                    <br></br>
                       (min. amount is 0.01 ETH)
                    <br></br>
                    (1 deposit is possible at the time)
                    <br></br>
                    <form onSubmit={(e) => {
                      e.preventDefault()
                      let amount = this.depositAmount.value
                      amount = amount * 10**18 //convert to wei
                      this.deposit(amount)
                    }}>
                      <div className='form-group mr-sm-2'>
                      <br></br>
                        <input
                          id='depositAmount'
                          step="0.01"
                          type='number'
                          ref={(input) => { this.depositAmount = input }}
                          className="form-control form-control-md"
                          placeholder='Ether Amount...'
                          required />
                      </div>
                      <button type='submit' className='btn btn-primary'>DEPOSIT</button>
                    </form>


                    </div></Tab>
                      <Tab eventKey="withdraw" title="Withdraw">
                  <div>
                  <br></br>
                   Withdraw your deposit + take interest
                    <br></br>
                    <br></br>
                  <div>
                    <button type='submit' className='btn btn-primary' onClick={(e) => this.withdraw(e)}>WITHDRAW</button>
                  </div></div></Tab>
              </Tabs>


              <span>Your Ehter Balance</span>
          <h4>{this.state.appEtherBalance}</h4>

           <span>Your DCB Tokens Balance</span>
          <h4>{this.state.appTokenBalance}</h4>

          <h5>Reload the page to see updated Balance</h5>

              </div>
            </main>
          </div>
        </div>
      </div>
    );
  }
}

export default App;